.wrap{
    margin:30px;
    border: 1px solid black;
    display:flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 500px;

}

header{
    top:0;
    position:fixed;
    a{
        padding: 30px;
        text-decoration: none;
        color:black;
    }
    a:hover{
        color:red;
    }
}

h2{
    display:flex;
    justify-content: center;
    align-items: center;

}

main{
    width:400px;
}

.won{
    width:100%; height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    gap:50px;

    .won-1{
        display:flex;
        justify-content: center;
        align-items:center;
        flex-wrap: wrap;
        width:100%;
        
        
        span{
            display: block;
            border:1px solid black;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            background-color: transparent;
            margin:0 auto;
        }
        p{text-align: center;}
        
    }

    .won-2{
        
        display:flex;
        justify-content: center;
        align-items:center;
        flex-wrap: wrap;
        width:100%;
        span{
            display: block;
            border:1px solid black;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            background-color: transparent;
            margin:0 auto;
        }
        p{text-align: center;}
        
        
    }
}

